function manageTooltipWidth() {
    const toolTip = document.querySelector('#web3-wallet-address');
    if (!toolTip) {
        console.error(`tooltips missing`);
        return;
    }
    if (isUserConnected()) {
        console.log('setting left to : 0');
        showAddress.style.setProperty("--custom-tooltip-left", "0px");
        return;
    }
    console.log('setting left to : negative');
    showAddress.style.setProperty("--custom-tooltip-left", "-100px");
}

async function addNetwork(chainId) {
    const BscChainId = chainId; //Bsc Testnet ID
    const chainName = "Testnet BSC";
    const currencyName = "test BNB";
    const currencySymbol = "tBNB";
    const rpcUrl = "https://speedy-nodes-nyc.moralis.io/d633c685eb50e4bb5f7bdcf8/bsc/testnet";
    const blockExplorerUrl = "https://testnet.bscscan.com/";
    await Moralis.addNetwork(
        BscChainId,
        chainName,
        currencyName,
        currencySymbol,
        rpcUrl,
        blockExplorerUrl
    );
}

