async function populateAmountRaisedinHTML() {
    // if (isUserConnected()) countValue = await getReferralsCount(userAddress);

    console.log(`ran - populateAmountRaisedinHTML`);

    // await Moralis.enableWeb3(); // blah
    let raisedAmount = await getTotalBnbAMount(); //which will be 'logged out' if user is logged out
    raisedAmount = +raisedAmount.toFixed(2);
    console.log(`raisedAmount = ${raisedAmount}`);
    let raisedAmountPercentage = percentageOfBNBRaised(raisedAmount);
    if (raisedAmountPercentage < 1) raisedAmountPercentage = 1;
    console.log(`raisedAmount = ${raisedAmount}`);
    console.log(`setting % = ${raisedAmountPercentage}`);
    // if user is logged-in we change the value of refferValue
    const progressBars = document.querySelectorAll('.sale-progress');
    progressBars && progressBars.forEach(el => {
        // in this example - the countValue = 0
        el.value = raisedAmountPercentage;
    });
    const raisedAmountHTML = document.querySelectorAll('.sale-progress-amount');
    raisedAmountHTML && raisedAmountHTML.forEach(el => {
        el.innerHTML = raisedAmount;
    })
    const raisedAmountPercentageHTML = document.querySelectorAll('.sale-progress-percentage');
    raisedAmountPercentageHTML && raisedAmountPercentageHTML.forEach(el => {
        el.innerHTML = raisedAmountPercentage;
    })
    const parentContainer = document.querySelectorAll('.sale-progress-val-parent');
    parentContainer.forEach(e => {
        let leftVal = +raisedAmountPercentage - 3;
        const maxLeftVal = +94;
        leftVal >= maxLeftVal ? leftVal = maxLeftVal : '';
        // console.log(`left = ${leftVal}`);
        // console.log(e);
        e.style.left = `${leftVal}%`;
    })
    const parentContainerMob = document.querySelectorAll('.sale-progress-val-parent-mob');
    parentContainer.forEach(e => {
        let leftVal = +raisedAmountPercentage - 3;
        const maxLeftVal = +65;
        leftVal >= maxLeftVal ? leftVal = maxLeftVal : '';
        // console.log(`left = ${leftVal}`);
        // console.log(e);
        e.style.left = `${leftVal}%`;
    })
}

